.popular{
    display: flex;
    flex-direction: column;
    align-items:center ;
    
    gap: 10px;
    margin-top: 300px;
}
.popular h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
    
}
.popular hr{
    
    height: 6px;
    border-radius: 10px;
    background: #252525;
    
}
.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 20px;
    width: 100px;
    height: 100px;
}
.populer-item{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 20px;
}
